// @import파일경로

$메인칼라 : #ff0000;
// 이거 때문에 사스문법 쓴다.

.red{
    color : $메인칼라;
}// 색상 외우는 방법?

div.container h4{
    color : blue;
}// 이건 사스문법 아니다.


div.container {
    h4{
        color : red;
    }
    p{
        color : green;
    }
}// 이게 nesting 문법 

.my-alert {
    background: #eeeeee;
    padding : 15px;
    border-radius: 5px;
    width: 100%;
    margin : auto;
}// 복붙을 하거나? scss문법을 쓰면?

// .my-alert-red로 작명하는게 좋다
.my-alert2 {
    @extend .my-alert;
    background : #ffe591;
}// .my-alert의 모든 속성을 붙여넣기 해주세요라는 의미이다.

.my-alert p {
    margin-bottom: 0;
}

@mixin 함수() {
    background: #eeeeee;
    padding : 15px;
    border-radius: 5px;
    width: 100%;
    margin : auto;
}// 이걸 그냥 함수처럼 쓸 수도 있다.
// 이걸쓰면, 코딩할 양이 줄어든다.

.mixintest{
    @include 함수()
}

